<template>
  <div class="songlist" @click="playMusic(item,tracks)">
    <span class="indexs">{{index+1}}</span>
    <div class="setbor">
      <div class="left">
        <h5>{{item.name}}</h5>
        <p>
          <span>{{item.ar[0].name}}</span>/
          <span>{{item.al.name}}</span>
        </p>
      </div>
      <div class="right">
        <i class="video-play"></i>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.songlist {
  overflow: hidden;
  color: #333;
  padding-left: 5px;
  display: flex;
  align-items: center;
  .indexs {
    float: left;

    text-align: center;
    width: 15%;
    font-size: 17px;
    color: #999;
  }
  .setbor {
    width: 85%;
    box-sizing: border-box;
    border-bottom: 1px rgba(0, 0, 0, 0.1) solid;
    float: left;
    line-height: 28px;
    display: flex;
  align-items: center;
    .left {
      float: left;
      width: 80%;
      box-sizing: border-box;
      padding-left: 10px;
      h5 {
        margin: 0;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
        font-size: 17px;
        overflow: hidden;
        font-weight: 400;
      }
      p {
        margin: 0;
        font-size: 12px;
        color: #888;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: normal;
      }
    }
    .right {
      width: 20%;
      float: right;
      text-align: center;
      .video-play {
        display: inline-block;
        width: 22px;
        height: 22px;
        background: url("../image/index_icon_2x.png") no-repeat;
        background-position: -24px 0;
        background-size: 166px 97px;
      }
    }
  }
}
</style>

<script>
export default {
  props: ["item", "index","tracks"],
  methods: {
    playMusic(item,tracks) {
    //  console.log(item.id);
      let that = this;

      axios
        .get("/song/url", {
          params: {
            id: item.id
          }
        })
        .then(function(response) {
          // console.log(response);
          that.$store.commit("Setcurrentmusicurl", response.data.data[0]);
          that.$store.commit("Setmusicid", item.id);
          that.$store.commit("SetcurrentMusic", item);
          that.$store.commit('SetcurrentMusiclist',tracks);
          that.$store.commit("SetNotChageMusicList",tracks);
          // console.log(that.personalizedList);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>