<template>
  <div class="hotSongList">
    <div class="part1">
      <img :src="coverImgUrl" alt srcset>
      <span>{{createTime}}</span>
    </div>
    <div class="part2">
      <SongList2
        v-for="(item, index) in tracks"
        :key="index"
        :item="item"
        :index="index"
        :tracks="tracks"
      ></SongList2>
    </div>
  </div>
</template>
<style lang="less" scoped>
.hotSongList {
  .part1 {
    position: relative;
    overflow: hidden;
    // width: 100%;
    // background: hsl(15, 38%, 48%);
    img {
      width: 100%;
      height: 150px;
      float: left;
    }
    span {
      position: absolute;
      bottom: 20px;
      left: 50px;
      margin-top: 10px;
      color: hsla(0, 0%, 100%, 0.8);
      font-size: 12px;
      transform: scale(0.91);
      transform-origin: left top;
    }
  }
}
</style>

<script>
import SongList2 from "@/components/SongList2.vue";
export default {
  components:{
    SongList2
  },
  data() {
    return {
      playlist: null,
      privileges: null,
      tracks: null,
      coverImgUrl:null,
      createTime:null
    };
  },
  created() {
    let that = this;
    axios
      .get("/top/list", {
        params: {
          idx: 1
        }
      })
      .then(function(response) {
        // console.log(response);
        that.playlist = response.data.playlist;
        that.privileges = response.data.privileges;
        that.tracks = response.data.playlist.tracks;
        that.coverImgUrl = response.data.playlist.coverImgUrl;
        that.createTime = response.data.playlist.createTime;
        // console.log(that.playlist);
        // console.log(that.privileges);
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>